import * as React from 'react';
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import TextField from '@mui/material/TextField';
import Paper from '@mui/material/Paper';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import LockOutlinedIcon from '@mui/icons-material/LockOutlined';
import Typography from '@mui/material/Typography';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import { LoginType, registerApi } from '../../services/auth.service';
import { toast } from 'react-toastify';
import { IconButton } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import { Visibility, VisibilityOff } from '@mui/icons-material';
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import * as Yup from 'yup';
import { useTranslation } from 'react-i18next';

function Copyright(props: any) {
  return (
    <Typography variant="body2" color="text.secondary" align="center" {...props}>
      {'Quantico © '}
      {new Date().getFullYear()}
      {'.'}
    </Typography>
  );
}

const defaultTheme = createTheme();

export default function SignIn() {
  const navigate = useNavigate();
  const [showPassword, setShowPassword] = React.useState(false);
  const { t } = useTranslation();


  const validationSchema = Yup.object().shape({
    username: Yup.string().trim()
      .required(t('Username is required') as string)
      .matches(/^[a-zA-Z0-9]+$/, t('Username must contain only letters and numbers') as string)
      .min(6, t('Username must be at least 6 characters') as string)
      .max(20, t('Username must not exceed 20 characters') as string),
    email: Yup.string().trim()
      .required(t('Email is required') as string)
      .email(t('Email is invalid') as string),
    password: Yup.string().trim()
      .required(t('Password is required') as string)
      .min(5, t('Password must be at least 5 characters') as string)
      .max(40, t('Password must not exceed 40 characters') as string),
    acceptTerms: Yup.bool().oneOf([true], t('Accept Terms is required') as string),
    acceptReceive: Yup.bool().oneOf([true], t('Accept Receive is required') as string),
    acceptRules: Yup.bool().oneOf([true], t('Accept Rules is required') as string),
  });

  const {
    register,
    handleSubmit,
    formState: { errors }
  } = useForm({
    resolver: yupResolver(validationSchema)
  });
  const onSubmit = async (data: any) => {
    try {
      const dataSend = {
        username: String(data.username).trim(),
        email: String(data.email).trim(),
        password: String(data.password).trim(),
        loginType: LoginType.Normal,
        casinoId: 1
      }
      const response = await registerApi(dataSend);
      if (response === 'Successfully') {
        toast.success(t('Register successfully'));
        setTimeout(() => {
          navigate('/auth');
        }, 800);
      }
      if (response === 'Email_existed') {
        toast.error(t('Email already exists, please try another email'));
      }
      if (response === 'Username_existed') {
        toast.error(t('Username existed, please try another username'));
      }
    }
    catch (error) {
      console.log("error", error);
      toast.error('Register failed');
    }
  };
  const handleClickShowPassword = () => {
    setShowPassword(!showPassword);
  };
  return (
    <ThemeProvider theme={defaultTheme}>
      <Grid container component="main" sx={{ height: '100vh' }} >
        <CssBaseline />
        <Grid
          item
          xs={false}
          sm={4}
          md={7}
          className='background-image'
        />
        <Grid item xs={12} sm={8} md={5} component={Paper} elevation={6} square>
          <Box
            sx={{
              my: 8,
              mx: 4,
              display: 'flex',
              flexDirection: 'column',
              alignItems: 'center',
            }}
          >
            <Avatar sx={{ m: 1, bgcolor: 'secondary.main' }}>
              <LockOutlinedIcon />
            </Avatar>
            <Typography component="h1" variant="h5">
              {t('Sign up')}
            </Typography>

            <form style={{
              width: '80%', // Fix IE 11 issue.
              marginTop: defaultTheme.spacing(1),
            }} onSubmit={handleSubmit(onSubmit)}>
              <div className="form-group">
                <TextField
                  margin="normal"
                  type="text"
                  fullWidth
                  id="user"
                  label={t("User name")}
                  autoComplete="user"
                  autoFocus
                  {...register('username')}
                  className={`form-control ${errors.username ? 'is-invalid' : ''}`}
                />
                <div className="invalid-feedback">{errors.username?.message}</div>
              </div>


              <div className="form-group">
                <TextField
                  margin="normal"
                  type="text"
                  fullWidth
                  id="email"
                  label={t("Email")}
                  autoComplete="email"
                  autoFocus
                  {...register('email')}
                  className={`form-control ${errors.username ? 'is-invalid' : ''}`}
                />
                <div className="invalid-feedback">{errors.email?.message}</div>
              </div>

              <div className="form-group">
                <TextField
                  margin="normal"
                  fullWidth
                  label={t("Password")}
                  type={showPassword ? 'text' : 'password'}
                  id="password"
                  autoComplete="current-password"
                  {...register('password')}
                  InputProps={{
                    endAdornment: (
                      <IconButton
                        aria-label="toggle password visibility"
                        onClick={handleClickShowPassword}
                      >
                        {showPassword ? <Visibility /> : <VisibilityOff />}
                      </IconButton>
                    ),
                  }}
                />
                <div className="invalid-feedback">{errors.password?.message}</div>
              </div>
              <div className="form-check">
                <input
                  type="checkbox"
                  {...register('acceptTerms')}
                  className={`form-check-input ${errors.acceptTerms ? 'is-invalid' : ''
                    }`}
                />
                <span className="form-check-label ml-2">
                  {t("I have read and agree to the Terms 1")}
                  <a href="/terms" target="_blank" rel="noopener noreferrer" className='form-check-label link-text'>
                    {t('information')}
                  </a>
                  {t("I have read and agree to the Terms 2")}
                </span>
                <div className="invalid-feedback" style={{
                  marginTop: '0px'
                }}>{errors.acceptTerms?.message}</div>
              </div>
              <div className="form-check">
                <input
                  type="checkbox"
                  {...register('acceptReceive')}
                  className={`form-check-input ${errors.acceptTerms ? 'is-invalid' : ''
                    }`}
                />
                <span className="form-check-label ml-2">
                  {t("Confirm receive")}
                </span>
                <div className="invalid-feedback" style={{
                  marginTop: '0px'
                }}>{errors.acceptReceive?.message}</div>
              </div>
              <div className="form-check">
                <input
                  type="checkbox"
                  {...register('acceptRules')}
                  className={`form-check-input ${errors.acceptTerms ? 'is-invalid' : ''
                    }`}
                />
                <span className="form-check-label ml-2">
                  {t("I have read and agree to the Terms 1")}
                  <a href="https://gameclient.italiancardclub.com/information/lobby" target="_blank" rel="noopener noreferrer" className='form-check-label link-text'>
                    {t('italianCardClub rules')}
                  </a>
                  {t("I accept them")}
                </span>
                <div className="invalid-feedback" style={{
                  marginTop: '0px'
                }}>{errors.acceptRules?.message}</div>
              </div>
              <div className="">
                <Button
                  type="submit"
                  fullWidth
                  variant="contained"
                  sx={{ mt: 3, mb: 2 }}

                >
                  {t('Sign up')}
                </Button>
              </div>
            </form>
            <span className='link-text text-center' onClick={() => navigate('/auth')}>
              {t('Back to login')}
            </span>
          </Box>
          <Copyright sx={{ mt: 5 }} />
        </Grid>
      </Grid>
    </ThemeProvider>
  );
}
